@keyframes rotate{
	0%{
		transform: rotate(0deg);
	}
	
	100%{
		transform: rotate(360deg);
	}
}
@keyframes lensmove{
	0%{
		transform: translate(0,0);
	}
	25%{
		transform: translate(10,-30px);
	}
	50%{
		transform: translate(30,-50px);
	}
	75%{
		transform: translate(50px,30px);
	}
	100%{
		transform: translate(0,0);
	}
}