.animation-object{
    position: absolute;
    width: 30px;
    height: 30px;
    opacity: 0.15;
    animation: socialanimation 20s infinite linear;
}
.animation-rotateY{
    position: absolute;
    width: 50px;
    height: 50px;
    opacity: 0.15;
    animation: animrotatey 20s infinite linear;
}
.animation-object-group{
    position: absolute;
    width: 75%;
    height: 75%;
    opacity: 0.3;
    animation: socialanimation 50s infinite linear;
}

@keyframes socialanimation {
    0%{
        transform: 
         rotateZ(0deg) ;
    }
    100%{
      transform: 
       rotateZ(360deg);
    }
}
@keyframes animrotatey {
    0%{
        transform: 
         rotateY(0deg) ;
    }
    100%{
      transform: 
       rotateY(360deg);
    }
}